
.popover {
  position: relative;
  -webkit-tap-highlight-color: transparent;
}
.popover__container {
  position: absolute;
  z-index: 4;
  bottom: 0;
  padding: 8px;
  background: #FFFFFF;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.5);
  border-radius: 8px;
}
.popover__container.left {
  left: 0;
}
.popover__container.right {
  right: 0;
}



