#map-container {
  #map {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    .mapboxgl-ctrl-group {
      @media @sm-and-down {
        display: none !important;
      }

      .mapboxgl-ctrl-compass {
        display: none !important;
      }
    }

    .mapboxgl-ctrl-top-right {
      right: auto;
      left: 8px;
      z-index: 1;

      @media @md-and-up {
        right: 12px;
        left: auto;
      }
    }
  }

  #map-loading-screen {
